<template>
  <div
    id="Tatuaje"
    class="d-flex justify-content-center align-content-center mt-5"
  >
    <div class="container">
      <div class="row">
        <div class="col-12 col-md-8">
          <div class="foto">
            <div>
              <b-carousel
                id="carousel-1"
                v-model="slide"
                :interval="10000"
                controls
                indicators
                background="#152230"
                img-width="1024"
                img-height="480"
                style="text-shadow: 1px 1px 2px #333;"
                @sliding-start="onSlideStart"
                @sliding-end="onSlideEnd"
              >
                <b-carousel-slide
                  v-for="(item, index) in tatuaje.imagenes"
                  :key="index"
                >
                  <template #img>
                    <img
                      class="d-block img-fluid w-100"
                      width="1024"
                      height="600"
                      style="
                        border-top-left-radius: 20px;
                        object-fit: cover;
                        height: 600px;
                      "
                      :src="src + item"
                      alt="tattoo img"
                    />
                  </template>
                </b-carousel-slide>
              </b-carousel>
            </div>
          </div>
        </div>
        <div class="col-12 col-md-4 pt-md-5 d-md-flex justify-content-end">
          <div
            class="d-md-grid d-flex flex-row justify-content-around flex-wrap flex-sm-nowrap"
          >
            <div class="c-body text-md-end text-start my-4">
              <h5 class="card-title m-2">{{ tatuaje.nombre }}</h5>

              <p class="card-text m-2" v-if="tatuaje.tatuado == true">
                Ya tatuado
              </p>
              <p class="card-text m-2" v-else>
                Sin tatuar
              </p>

              <p
                id="desconocido"
                class="card-text m-2"
                v-if="nombreAutor == ''"
              >
                Desconocido
              </p>
              <p id="autor" class="card-text m-2" v-else>
                <a :href="paginaAutor">{{ nombreAutor }}</a>
              </p>

              <p class="card-text m-2">{{ tatuaje.aboutTattoo }}</p>
            </div>

            <div class="cita">
              <button class="btn btn-primary" @click="cita">
                ðŸ‘† Pide cita!
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      slide: 0,
      sliding: null,
      src: 'https://tattoo-backend.vercel.app/',
      tatuaje: {
        nombre: '',
        tatuado: Boolean,
        autor: '',
        imagenes: Array(),
        imagen: null,
        precio: 0,
        aboutTattoo: '',
      },
      idT: '',
      paginaAutor: '',
      nombreAutor: '',
    }
  },
  created() {
    this.idT = location.href.split('?id=')[1]
    this.axios
      .get('tatuajes/listar/' + this.idT)
      .then((res) => {
        this.tatuaje = res.data
        this.paginaAutor = '/usuario?id=' + this.tatuaje.autor
      })
      .catch((e) => {
        console.log(e.response)
      })
      .finally((e) => {
        this.axios
          .get('/usuarios/tatuadores')
          .then((res) => {
            var varaux = res.data
            varaux.forEach((element) => {
              if (element._id == this.tatuaje.autor) {
                this.nombreAutor = element.nombre
              }
            })
          })
          .catch((e) => {
            console.log(e)
          })
      })
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true
    },
    onSlideEnd(slide) {
      this.sliding = false
    },
    cita() {
      window.location.href =
        '/agregarCita?t=' + this.idT + '&a=' + this.tatuaje.autor
    },
  },
  mounted() {
    setTimeout(() => {
      if (this.tatuaje.imagenes.length <= 1) {
        document.querySelector('#carousel-1 > a.carousel-control-next').remove()
        document.querySelector('#carousel-1 > a.carousel-control-prev').remove()
      } else {
        document.querySelector(
          '#carousel-1 > a.carousel-control-next > span.sr-only',
        ).innerHTML = ''
        document.querySelector(
          '#carousel-1 > a.carousel-control-prev > span.sr-only',
        ).innerHTML = ''
      }
    }, 50)
  },
}
</script>

<style>
/* Card tatuaje */
div#Tatuaje .container {
  background-color: rgb(33, 37, 41);
  width: 800px;
  margin: 20px;
  padding: 0;
  padding-bottom: 0;
  padding-left: 0;
  border-radius: 20px;
}
 /*Foto */
div.carousel-item > img {
  height: 600px;
  max-width: 600px;
  border-radius: 20px 0 0 20px;
  margin: 0 0 0 0;
}
@media (max-width: 770px) {
  div.carousel-item > img {
    max-width: 552px;
    border-radius: 20px 20px 0 0 !important;
  }
}
@media (max-width: 770px) {
  .cita {
    display: flex;
    justify-content: center;
    align-content: center;
    height: 38px;
    margin-top: 60px;
  }
}
.c-Body {
  height: 800px;
  width: 100px;
}

/* Estilo texto card */

p#autor a {
  cursor: pointer;
  background: linear-gradient(
    90deg,
    rgba(232, 255, 0, 1) 0%,
    rgba(23, 212, 60, 1) 50%,
    rgba(0, 176, 255, 1) 100%
  );
  font-weight: 600;
  font-size: 40px;
  inset: 0;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
p#autor > a:hover {
  text-decoration: none;
  background: linear-gradient(
    90deg,
    rgba(232, 255, 0, 1) 0%,
    rgba(23, 212, 60, 1) 50%,
    rgba(0, 176, 255, 1) 100%
  );
  -webkit-text-fill-color: #212529;
}
p#desconocido {
  cursor: pointer;
  background: linear-gradient(
    90deg,
    rgba(255, 0, 0, 1) 0%,
    rgba(221, 29, 207, 1) 50%,
    rgba(255, 0, 0, 1) 100%
  );
  font-weight: 600;
  font-size: 35px;
  inset: 0;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

p#desconocido:hover {
  text-decoration: none;
  background: linear-gradient(
    90deg,
    rgba(255, 0, 0, 1) 0%,
    rgba(221, 29, 207, 1) 50%,
    rgba(255, 0, 0, 1) 100%
  );
  -webkit-text-fill-color: #212529;
}

@media (max-width: 770px) {
  div#Tatuaje > div.container {
    width: 600px !important;
    height: 900px !important;
  }
}
@media (max-width: 580px) {
  div#Tatuaje > div.container {
    height: 1000px !important;
  }
}
@media (max-width: 766px) {
  div.c-body ~ div.cita > button {
    width: 113.17px !important;
  }
}
@media (max-width: 500px) {
  div.c-body ~ div.cita {
    margin: 20px !important;
  }
}
@media (max-width: 380px) {
  div#Tatuaje > div.container {
    height: 1150px !important;
  }
}
</style>
